@import "../../styles/AppDesign.scss";

.subs-popup{
    position:fixed;
    width:100%;
    height:100vh;
    background:rgba(#000, 0.5);
    z-index: 2000;
    top:0;
    left:0;
    pointer-events: none;
    opacity: 0;
    transition: .4s opacity;
    &.is-open {
        pointer-events: all;
        opacity: 1;
    }
}
.subs-popup__overlay{
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height:100%;
    z-index: 0;
    font-size: 0;
    background: transparent;
    border:0;
    opacity: 0;
    cursor: pointer;
    @include mobileView(){
        background-color:$bgWhite;
        opacity: 1;
        margin-top:-25vh;
        height:150vh;
        transform: translateX(101%);
        transition: .4s;
        .subs-popup.is-open &{
            transform: translateX(0);
        }
    }
}
.subs-popup__body{
    position: absolute;
    top:0;
    right:0;
    width: 50%;
    height:100%;
    z-index: 10;
    overflow:auto;
    transform: translateX(101%);
    transition: .4s;
    box-shadow: -20px 0px 30px 0px rgba(0, 0, 0, 0.75);
    padding: 35px 50px;


    // background: $bgWhite;
    @include tabletView(){
        width: 75%;
    }
    @include mobileView(){
        padding: 20px 20px;
        width: 100%;
    }
    &:after,
    &:before {
        content: '';
        display:block;
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        pointer-events: none;
        // background: url('/img/landing/bg-curve-black.svg') no-repeat right top;
        // background-size: 200% auto;
        // @include mobileView() {
        //   background-image: url('/img/landing/bg-curve-black-mobile.svg');
        // }
        // transition: .5s;
        // z-index: 0;
        // pointer-events: none;
        // opacity: 0.15;
    }
    &:after{
        z-index: 0;
        background-color:$bgWhite;
        @include mobileView(){
            display:none;
        }
    }
    &:before{
        z-index: 1;
        background: no-repeat right center;
        background-image: url('/img/landing/bg-curve-black.svg'); 
        // background-size: 200% auto;
        opacity: 0.15;
        background-size: cover;
        @include mobileView() {
          background-image: url('/img/landing/bg-curve-black-mobile.svg');
          opacity: 1;
        }
    }
    .subs-popup.is-open &{
        transform: translateX(0);
    }
}
.subs-popup__head{
    position: relative;
    margin-bottom:80px;
    z-index: 30;
    pointer-events: none;
}
.subs-popup__close{
    display:block;
    $size:30px;
    width:$size;
    height:$size;
    padding:0;
    font-size: 0;
    border: 0;
    background: url('/img/icon/icon-close.svg') transparent no-repeat center center;
    background-size: contain;
    cursor: pointer;
    z-index: 30;
    pointer-events: all;
    @include mobileView(){
        $size:20px;
        width:$size;
        height:$size;
    }
}
.subs-popup__form{
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    z-index: 5;
    iframe{
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        border: 0;
    }
}