@import "../../styles/AppDesign.scss";

.landing {
    position: relative;
    height: 100vh;
    width: 100%;
    z-index: 500;
    //overflow: hidden;
    top: 0;
    left: 0;
}

.landing__inner {
    position: fixed;
    // background:$main;
    background: $bgWhite;
    top: 0;
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    opacity: 0;
    @include mobileView(){
        padding-left:54px;
        padding-right:40px;
    }
    @include navAndSloganTopPadding(0, 15px, 0);
    .landing.is-active & {
        .page-ready & {
            opacity: 1;
        }
    }
}
.landing__slogans{
    @include mobileView(){
        width:100%;
    }
}
.landing__slogan {
    display: block;
    opacity: 0;
    $initialDelay: .15s;
    $delayInterval: .15s;
    transform: translateY(200%);
    // transition: transform .5s, opacity .7s;
    height: 5.9vw;
    @include tabletView(){
        height: 6.5vw;
    }
    @include mobileView(){
        height: 9.2vw;
    }

    .landing.is-active & {
        .page-ready & {
            // opacity: 1;
            // transform: translateY(0);
            animation:landing-slogan-move .5s forwards;
            &--1 {
                animation-delay: $initialDelay + $delayInterval * 0;
                margin-top: -2vh;
                @include tabletView(){
                    margin-top: -2vw;
                }
            }
        
            &--2 {
                animation-delay: $initialDelay + $delayInterval * 1;
            }
        
            &--3 {
                animation-delay: $initialDelay + $delayInterval * 2;
            }
        }
    }

    @keyframes landing-slogan-move {
        0% {
            opacity: 0;
            transform: translateY(150%);
        }
        100% {
            opacity: 1;
            transform: translateY(0);
        }
    }


    // &--1 {
    //     animation-delay: $initialDelay + $delayInterval * 0;
    // }

    // &--2 {
    //     animation-delay: $initialDelay + $delayInterval * 1;
    // }

    &--3 {
        margin-top: 3vh;
        @media (max-width: 1280px) {
            margin-top: 2.25vh;
        }
        @include tabletView(){
            margin-top: 2.3vw;
        }
        @include mobileView(){
            margin-top: 3vw;
        }
        // @media (max-width: 480px) {
        //     margin-top: 1.45vw;
        // }
    }
}